.header {
  // Hamburger Open Styles
  &.open {
    .overlay {
      display: block;
    }
    .header__menu {
      > span:first-child {
        transform: rotate(45deg);
      }
      > span:nth-child(2) {
        opacity: 0;
      }
      > span:last-child {
        transform: rotate(-45deg);
      }
    }
  }
  .overlay {
    display: none;
    position: fixed;
    top: 3.75rem;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient($darkBlue, transparent);
  }
  nav {
    padding: 1.0625rem 1.5rem;
  }

  &__logo {
    img {
      width: 8.8125rem;
      height: 1.375rem;
    }
  }

  &__menu {
    // Mobile Menu
    > span {
      display: block;
      width: 26px;
      height: 2.5px;
      background-color: $darkBlue;
      transition: all 300ms ease-in-out;
      transform-origin: 3px 1px;
      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }
  &__links {
    a {
      position: relative;
      font-size: $font-sm;
      color: $grayishBlue;
      transition: color 300ms ease-in-out;
      &:not(:last-child) {
        margin-right: 32px;
      }
      &::before {
        position: absolute;
        content: '';
        display: block;
        height: 5px;
        left: 0;
        right: 0;
        bottom: -30px;
        background: linear-gradient(to right, $limeGreen, $brightCyan);
        opacity: 0;
        transition: opacity 300ms ease-in-out;
      }
      &:hover {
        color: $darkBlue;
        &::before {
          opacity: 1;
        }
      }
    }
  }
  &__cta {
    font-size: $font-sm;
    color: $white;
    transition: opacity 300ms ease-in-out;
    &:hover {
      opacity: 0.75;
    }
  }
}
